/*======================================================================
      Global
  ======================================================================*/

* {
  padding: 0;
  margin: 0;
  outline: none;
}

html {
  height: 100%;
  font-family: "Adobe Heiti Std", "microsoft yahei", Arial, "Helvetica Neue", Helvetica, sans-serif;
  color: #fff;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

body {
  height: 100%;
  overflow: hidden;
  background-color: #080929;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

p,
a,
button,
span {
  -webkit-font-smoothing: antialiased;
}

input {
  outline: none;
}

.pa {
  position: absolute !important;
}

.full-size,
.background,
.full-car,
.full-car .hd,
.scene,
.scene01 .logo,
.scene01 .entry-list,
.scene03 .car,
.scene04 .car {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  min-width: 1920px;
}

