.scene01.active .time {
  opacity: 1;
  -webkit-transition: all .6s 3.6s;
  transition: all .6s 3.6s;
}

.scene01.active .logo .img01,
.scene01.active .logo .img02 {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  opacity: 1;
}

.scene01.active .logo .img01 {
  -webkit-transition: all 1s 1s;
  transition: all 1s 1s;
}

.scene01.active .description {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  -webkit-transition: all 1s 1.6s;
  transition: all 1s 1.6s;
  opacity: 1;
}

.scene01.active .item01 {
  -webkit-animation: bounceInUp 0.6s 1.8s forwards;
  animation: bounceInUp 0.6s 1.8s forwards;
}

.scene01.active .item02 {
  -webkit-animation: bounceInUp 0.6s 1.95s forwards;
  animation: bounceInUp 0.6s 1.95s forwards;
}

.scene01.active .item03 {
  -webkit-animation: bounceInUp 0.6s 2.1s forwards;
  animation: bounceInUp 0.6s 2.1s forwards;
}

.scene01.active .item04 {
  -webkit-animation: bounceInUp 0.6s 2.25s forwards;
  animation: bounceInUp 0.6s 2.25s forwards;
}

.scene01.active .item05 {
  -webkit-animation: bounceInUp 0.6s 2.4s forwards;
  animation: bounceInUp 0.6s 2.4s forwards;
}

.scene01.active .svg .bar {
  -webkit-transition: stroke-dashoffset 1.2s ease-out;
  transition: stroke-dashoffset 1.2s ease-out;
}

.scene01.active .item01 .bar {
  stroke-dashoffset: 223px;
  -webkit-transition-delay: 3.4s;
  transition-delay: 3.4s;
  -webkit-transform: rotate(20deg);
  transform: rotate(20deg);
  -webkit-transform-origin: center center;
  transform-origin: center center;
}

.scene01.active .item02 .bar {
  stroke-dashoffset: 89.2px;
  -webkit-transition-delay: 3.4s;
  transition-delay: 3.4s;
  -webkit-transform: rotate(-90deg);
  transform: rotate(-90deg);
  -webkit-transform-origin: center center;
  transform-origin: center center;
}

.scene01.active .item03 .bar {
  stroke-dashoffset: 334.5px;
  -webkit-transition-delay: 3.4s;
  transition-delay: 3.4s;
  -webkit-transform: rotate(-90deg);
  transform: rotate(-90deg);
  -webkit-transform-origin: center center;
  transform-origin: center center;
}

.scene01.active .item04 .bar {
  stroke-dashoffset: 200.7px;
  -webkit-transition-delay: 3.4s;
  transition-delay: 3.4s;
  -webkit-transform: rotate(-120deg);
  transform: rotate(-120deg);
  -webkit-transform-origin: center center;
  transform-origin: center center;
}

.scene01.active .item05 .bar {
  stroke-dashoffset: 89.2px;
  -webkit-transition-delay: 3.4s;
  transition-delay: 3.4s;
  -webkit-transform: rotate(160deg);
  transform: rotate(160deg);
  -webkit-transform-origin: center center;
  transform-origin: center center;
}

@-webkit-keyframes bounceInUp {
  from, 60%, 75%, 90%, to {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }

  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 30px, 0);
    transform: translate3d(0, 30px, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

@keyframes bounceInUp {
  from, 60%, 75%, 90%, to {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }

  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 30px, 0);
    transform: translate3d(0, 30px, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

