/*======================================================================
      Components
  ======================================================================*/

.select {
  position: relative;
  width: 214px;
  color: #516a96;
}

.select.active {
  z-index: 1000;
}

.select.active .icon {
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
}

.select.active .select-bd {
  display: block;
  z-index: 1000;
}

.select.scroll .bd {
  padding-bottom: 30px;
}

.select.scroll .bd .triangle {
  display: block;
}

.select-hd {
  height: 55px;
  padding: 0 22px;
  line-height: 55px;
  font-size: 18px;
  background: url("../images/select-hd-bg.png") no-repeat;
  cursor: pointer;
}

.select-hd .icon {
  position: absolute;
  right: 19px;
  top: 24px;
  width: 17px;
  height: 10px;
  background: url("../images/icon-triangle-down.png") no-repeat;
  -webkit-transition: all .3s;
  transition: all .3s;
  -webkit-transform-origin: center center;
  transform-origin: center center;
}

.select-bd {
  display: none;
  position: absolute;
  left: 0;
  right: 0;
  top: 55px;
}

.select-bd .bd {
  position: relative;
  overflow: hidden;
  padding: 5px 0;
  background: url("../images/select-bd-bg.png") repeat-y;
}

.select-bd .bd .container {
  width: 100%;
  padding-right: 20px;
  max-height: 200px;
  overflow-y: scroll;
}

.select-bd .bd .triangle {
  display: none;
  position: absolute;
  width: 19px;
  height: 11px;
  left: 100px;
  bottom: 10px;
  background: url("../images/icon-triangle-down.png") no-repeat;
  -webkit-animation: breath 1.2s .2s infinite;
  animation: breath 1.2s .2s infinite;
}

.select-bd .item {
  padding: 0 22px;
  height: 30px;
  line-height: 30px;
  margin-bottom: 3px;
  cursor: pointer;
}

.select-bd .item:hover {
  background: url("../images/select-bd-selected.png") repeat-y 10px 0;
}

.select-bd .bottom {
